import React from 'react'
import Link from 'gatsby-link'
import District from '../../components/District'

const DistrictNo9 = ({ data }) => {
  return <District nodes={data.allLocationsXlsxSheet1.edges} />
}

export default DistrictNo9

export const DistrictNo9Query = graphql`
  query DistrictNo9Query {
    allLocationsXlsxSheet1(filter: { district: { eq: "9" } }) {
      edges {
        node {
          plf_id
          complete_address
          longitude
          latitude
          city
          ward
          district
          planned_launch_date
          week
          status
          contact_email
          notes
        }
      }
    }
  }
`



